.country-list.hide, #rc-connect, .rc-button {
  display: none;
}

#maincontainer {
  float: none;
  margin: 10px 10px 10px 10px;
}

#main {
  position: relative;
  overflow: hidden;
  width: 718px;
  height: 280px;

  a {
    text-decoration: none !important;
    border: none !important;
    outline: none !important;

    img {
      text-decoration: none !important;
      border: none !important;
      outline: none !important;
    }
  }

  h4 {
    font-family: Arial, Helvetica, sans-serif !important;
    font-size: 16px !important;
    margin: 0 !important;
    padding: 0 !important;
    color: #fff !important;
  }

  .cs-title {
    font-family: Arial, Helvetica, sans-serif !important;
    font-size: 12px !important;
    line-height: normal !important;
    letter-spacing: normal !important;
    color: #fff !important;
    background: #222;
    position: absolute;
    width: 708px;
    padding: 10px;
  }
}

#maincontainer .cs-buttons {
  font-size: 0;
  float: left;
  clear: both;
  margin: 10px 0 10px 300px;
}

#cs-button-main {
  z-index: 999;
  outline: none;
}

#maincontainer {
  .cs-buttons {
    font-size: 0;
    float: left;
    padding: 10px;
    outline: none !important;

    a {
      float: left;
      width: 15px;
      height: 15px;
      margin-left: 5px;
      text-indent: -1000px;
      outline: none !important;
      background: url('../img/default_style.png') no-repeat;
      background-position: top;

      &:hover {
        top: 15px;
        outline: none !important;
        background: url('../img/default_style.png') no-repeat;
        background-position: bottom;
      }
    }
  }

  a.cs-active {
    outline: none !important;
    background: url('../img/default_style.png') no-repeat;
    background-position: bottom;
  }
}

#main {
  .cs-prev, .cs-next {
    outline: none;
  }

  .cs-prev, .cs-next {
    font-family: 'Courier New', Courier, monospace;
    font-size: 28px !important;
    font-weight: bold;
    padding: 0 10px !important;
    color: #fff !important;
    border-radius: 5px;
    background: #222 !important;
  }

  border: 0 solid #fff;
  border-radius: 0;

  img {
    border: 0 solid #fff;
    border-radius: 0;
  }
}

#rc-phone-button.rc-pressed:hover {
  background-color: rgb(19, 133, 33) !important;
}
